import {
    Component,
    ComponentFactoryResolver,
    ChangeDetectorRef,
    ViewChild,
    AfterViewInit,
    OnInit,
} from '@angular/core';
import { LayoutController } from './layout-controller';
import { EvAdLAyoutDirective } from './layout.directive';

@Component({
  selector: 'eve-layout',
  template: `
    <ng-template eve-layout-ad-host></ng-template>
  `,
})
export class EveLayoutComponent implements AfterViewInit, OnInit {

  @ViewChild(EvAdLAyoutDirective)
    adHost!: EvAdLAyoutDirective;

  constructor (
    private componentFactoryResolver: ComponentFactoryResolver,
    private cdr: ChangeDetectorRef,
    private lyCtrl: LayoutController,
  ) { }

  ngAfterViewInit () {
    this.loadComponent();
    this.cdr.detectChanges();
  }
  ngOnInit () {
    this.lyCtrl.layout.watch().subscribe(() => { this.loadComponent(); });
  }
  loadComponent () {
    const layout = this.lyCtrl.layout.getActive();
    if (layout !== false) {
      const componentFactory = this.componentFactoryResolver.resolveComponentFactory(layout.component);

      const viewContainerRef = this.adHost.viewContainerRef;
      viewContainerRef.clear();

      viewContainerRef.createComponent(componentFactory);
    } else {
      throw (
        new Error('No se encontro layouts para cargar por defecto, \n revise la configuracion en su modulo principal')
      );
    }
  }
}
