import { Subject } from 'rxjs';
import { CoreBrowserStorage } from 'everiscore';
import { Observable } from 'rxjs/Observable';
import { ILayoutsComponent } from '../layout.interface';
import { LayoutEncrypt } from './encrypt.model';

export class Layout {
  private state = new Subject<ILayoutsComponent>();
  private $state = this.state.asObservable();
  constructor(
    private layouts: ILayoutsComponent[],
    private sessionStorage: CoreBrowserStorage,
  ) { }

  getActive() {
    const persistLayout = this.sessionStorage.get('layout') || null;
    let ly;
    if (persistLayout === null) {
      ly = this.layouts.filter(layout => layout.default === true);
    } else {
      ly = this.layouts.filter((layout) => {

        return layout.name.toString().toLowerCase() === LayoutEncrypt.unencryptStorage(persistLayout).toString().toLowerCase();
      });
    }

    return ly.length > 0 ? ly[0] : false;
  }

  getItems(): ILayoutsComponent[] {
    return this.layouts;
  }

  watch(): Observable<ILayoutsComponent> {
    return this.$state;
  }

  changeLayout(name: string) {
    const ly = this.layouts.filter(layout => layout.name.toLowerCase() === name.toLowerCase());
    if (ly.length > 0) {
      this.sessionStorage.set('layout', LayoutEncrypt.encryptStorage(ly[0].name));
      this.state.next(ly[0]);
    } else {
      throw (
        new Error(`El layout ${name}, no existe`)
      );
    }
  }
}
