import { IMenuLayout } from '../layout.interface';
import { CoreBrowserStorage } from 'everiscore';

export class MenuLayout {
  constructor(
    private menu: IMenuLayout[],
    private sessionStorage: CoreBrowserStorage,
  ) { }

  addItems(items: IMenuLayout[]) {
    this.menu = [...this.menu, ...items];
    this.sessionStorage.set('menu', this.menu);
  }

  get items(): IMenuLayout[] {
    const persistMenu = this.sessionStorage.get('menu') || null;
    if (persistMenu) {
      this.menu = this.sessionStorage.get('menu') || '';
    }
     return this.menu;
  }
}
